:root{
    --color-bg: #0e141b;
  --color-bg-offset: #252526;
  --color-text: #ffffff;
  --color-text-offset: #818a91;
  --color-border: #2d2d2d;
  --color-primary: #525eff;
  --color-primary-offset: #292dff;
  --color-secondary: #43a9a3;
  }

  *{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }

  .nav__header{
    position: fixed;
    width: 100%;
    top: 0;
    background-color: #0e141b;
    background-color: var(--color-bg);
    z-index: 1;
  }

  #nav__brand-title-a{

    cursor: pointer;
    text-decoration: none;

  }

  .nav{

    padding: 20px;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  
  }

  .nav__brand{

    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 250px;
    height: 100%;
  
  }

  .nav__brand-logo--container{


    height: 50px;
    width: 50px;
    border-radius: 50%;
    overflow: hidden;
  
  }

  .nav__brand-logo{

    margin: auto;
    width: 50px;
    height: 50px;
  
  }

  .nav__brand-title{

    color: #ffffff;

    color: var(--color-text);
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
  
  }

  .nav__ul{

    width: 40%;
    display: flex;
    justify-content: space-around;
  
  }

  .nav__ul-li{

    list-style: none;
    font-family: 'Roboto', sans-serif ;
    width: 20%;
  
  }

  .nav__ul-li a{

    width: 20%;
    text-decoration: none;
    color: #ffffff;
    color: var(--color-text);
    position: relative;
    font-size: 15px;
  
  }

  .nav__ul-li a::before{

    content: '';
    background-color: #818a91;
    background-color: var(--color-text-offset);
    position: absolute;
    height: 5px;
    width: 0;
    top:20px;
    transition: all 0.2s ease;
  
  }

  .nav__ul-li a:hover::before{

    width: 100%;
  
  }

  .before-selected::after{

    content: '';
    position: absolute;
    height: 5px;
    top:20px;
    right: 0px;
    width: 100%;
    background-color: #525eff;
    background-color: var(--color-primary);
    z-index: 1;
  
  }

  .nav__ul-li-a--btn{

    width: 100%;
    text-decoration: none;
    color: #ffffff;
    color: var(--color-text);
    position: relative;
    font-size: 15px;
    background: transparent;
    border: none !important;
    font-family: 'Roboto', sans-serif ;
    cursor: pointer;
  
  }

/* elementos responsive */

  .nav__resposive-btn{

    display: none;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    background-color: #252526;
    background-color: var(--color-bg-offset);
    border: none;
    border-radius: 50%;
    cursor: pointer;
  
  }

  .hamburguesa__container{

    display: none;
    position: relative;
    width: 24px;
    height: 16px;
    z-index: 2;

  }

  .hamburguesa{

    display: block;
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #ffffff;
    background-color: var(--color-text);
    left: 0;
  
    -webkit-animation-duration: 0.5s ;
  
            animation-duration: 0.5s ;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  
  }
  
  #top-line{
  
    bottom:16px;
  
  }
  
  #middle-line{
    bottom: 8px;
  }
  
  #bottom-line{
    bottom: 0px;
  }
  
  .top-line{
    -webkit-animation-name: top-line-animation;
            animation-name: top-line-animation;
  }
  
  .middle-line{
    -webkit-animation-name: middle-line-animation;
            animation-name: middle-line-animation;
  }
  
  .bottom-line{
    -webkit-animation-name: bottom-line-animation;
            animation-name: bottom-line-animation;
  }
  
  .top-line-close{
    -webkit-animation-name: top-line-animation-close;
            animation-name: top-line-animation-close;
  }
  
  .middle-line-close{
    -webkit-animation-name: middle-line-animation-close;
            animation-name: middle-line-animation-close;
  }
  
  .bottom-line-close{
    -webkit-animation-name: bottom-line-animation-close;
            animation-name: bottom-line-animation-close;
  }
  
  
  @-webkit-keyframes top-line-animation{
  
    50%, 100%{
      bottom: 8px;
      -webkit-transform: translateY(2px) rotate(-45deg);
              transform: translateY(2px) rotate(-45deg);
    }
  
  }
  
  
  @keyframes top-line-animation{
  
    50%, 100%{
      bottom: 8px;
      -webkit-transform: translateY(2px) rotate(-45deg);
              transform: translateY(2px) rotate(-45deg);
    }
  
  }
  
  @-webkit-keyframes bottom-line-animation{
  
    50%, 100%{
      bottom: 8px;
      -webkit-transform: translateY(2px) rotate(45deg);
              transform: translateY(2px) rotate(45deg);
    }
  
  }
  
  @keyframes bottom-line-animation{
  
    50%, 100%{
      bottom: 8px;
      -webkit-transform: translateY(2px) rotate(45deg);
              transform: translateY(2px) rotate(45deg);
    }
  
  }
  
  @-webkit-keyframes middle-line-animation{
  
    20%, 100%{
      
      width: 0;
      opacity: 0;
    }
  
  }
  
  @keyframes middle-line-animation{
  
    20%, 100%{
      
      width: 0;
      opacity: 0;
    }
  
  }
  
  @-webkit-keyframes top-line-animation-close{
  
    0%{
      bottom: 8px;
      -webkit-transform: translateY(2px) rotate(-45deg);
              transform: translateY(2px) rotate(-45deg);
    }
  
    50%,100%{
      bottom: 16px;
      -webkit-transform: translateY(0px) rotate(0deg);
              transform: translateY(0px) rotate(0deg);
    }
  
  }
  
  @keyframes top-line-animation-close{
  
    0%{
      bottom: 8px;
      -webkit-transform: translateY(2px) rotate(-45deg);
              transform: translateY(2px) rotate(-45deg);
    }
  
    50%,100%{
      bottom: 16px;
      -webkit-transform: translateY(0px) rotate(0deg);
              transform: translateY(0px) rotate(0deg);
    }
  
  }
  
  @-webkit-keyframes middle-line-animation-close{
  
    0%{
      
      width: 0;
      opacity: 0;
    }
    20%,100%{
      
      width: 100%;
      opacity: 1;
    }
  
  }
  
  @keyframes middle-line-animation-close{
  
    0%{
      
      width: 0;
      opacity: 0;
    }
    20%,100%{
      
      width: 100%;
      opacity: 1;
    }
  
  }
  
  @-webkit-keyframes bottom-line-animation-close{
  
    0%{
      bottom: 8px;
      -webkit-transform: translateY(2px) rotate(45deg);
              transform: translateY(2px) rotate(45deg);
    }
  
    50%,100%{
      bottom: 0px;
      -webkit-transform: translateY(0px) rotate(0deg);
              transform: translateY(0px) rotate(0deg);
    }
  
  }
  
  @keyframes bottom-line-animation-close{
  
    0%{
      bottom: 8px;
      -webkit-transform: translateY(2px) rotate(45deg);
              transform: translateY(2px) rotate(45deg);
    }
  
    50%,100%{
      bottom: 0px;
      -webkit-transform: translateY(0px) rotate(0deg);
              transform: translateY(0px) rotate(0deg);
    }
  
  }


  #nav-responsive-container{

    position: absolute;
    top: 0;
    top: 20px;
    /* right:0; */
    border-radius: 50%;
    right: 20px;
    /* bottom: 0; */
    margin-top: 0;
    width: 50px;
    /* width: 100vw; */
    height: 50px;
    /* height: 100vh; */
    background-color: #000;
    
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    opacity: 0;
    z-index: 1;
  
  }


  .nav__responsive-animation{
  
    -webkit-animation-name: desplegar;
  
            animation-name: desplegar;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  
  }

  @-webkit-keyframes desplegar{

    0%{
  
      opacity: 0;
      width: 50px;
      height: 50px;
      
      
    }
  
    20%{
  
      width: 50vh;
      height: 50vh;
      border-radius: 0%;
      opacity: 1;
      top: 0%;
      right:0;

    }
    
    40%{
  
      opacity: 1;
      width: 100vh;
      height: 100vh;
      border-radius: 0%;
  
    }
  
    100%{
  
      opacity: 1;
      width: 100vw;
      height: 100vh;
      border-radius: 0%;
      top: 0%;
      right:0;
    }
  
  }

  @keyframes desplegar{

    0%{
  
      opacity: 0;
      width: 50px;
      height: 50px;
      
      
    }
  
    20%{
  
      width: 50vh;
      height: 50vh;
      border-radius: 0%;
      opacity: 1;
      top: 0%;
      right:0;

    }
    
    40%{
  
      opacity: 1;
      width: 100vh;
      height: 100vh;
      border-radius: 0%;
  
    }
  
    100%{
  
      opacity: 1;
      width: 100vw;
      height: 100vh;
      border-radius: 0%;
      top: 0%;
      right:0;
    }
  
  }
  
  #nav__responsive{
  
    position: absolute;
    opacity: 0;
    
  }
  
  .nav__responsive-none{
  
    display: none;
  
  }
  
  .nav__responsive-yes{
  
    display: block;
    -webkit-animation-name: subir-nav;
            animation-name: subir-nav;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
    -webkit-animation-duration: 0.5s ;
            animation-duration: 0.5s ;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  
  }
  
  @-webkit-keyframes subir-nav{
  
    0%{
      opacity: 0;
      -webkit-transform: translateY(100px);
              transform: translateY(100px);
    }
    100%{
      opacity: 1;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
  
  }
  
  @keyframes subir-nav{
  
    0%{
      opacity: 0;
      -webkit-transform: translateY(100px);
              transform: translateY(100px);
    }
    100%{
      opacity: 1;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
  
  }
  
  .nav__responsive-ul-li{

    list-style: none;
    width: 70%;
    position: relative;
    margin-bottom: 20px;
  
  }
  
  .responsive-ul-none{
    display: none;
  }
  
  .nav__responsive-ul-li a{
    position: relative;
    padding-left: 100px;
  }
  
  .nav__responsive-ul-li a::before{
  
    content: '';
    background-color: #818a91;
    background-color: var(--color-text-offset);
    position: absolute;
    height: 7px;
    width: 0%;
    top: 30px;
    left: 0;
    transition: all 0.2s ease;
  
  }
  
  
  
  .nav__responsive-ul-li a:hover::before{
  
    width: 100px;
  
  }
  
  .nav__responsive-ul-li a button{
  
    font-size: 40px;
    text-align: left;
    font-family: 'Merriweather', serif;
    transition: all 0.2s ease;
  
  }
  
  
  
  .before__responsive-selected::after{
  
    content: '';
    position: absolute;
    height: 7px;
    top: 30px ;
    left: 0px;
    width: 100px;
    background-color: #525eff;
    background-color: var(--color-primary);
    z-index: 1;
  
  }
  

  @media (max-width: 940px) {
      
    .nav__ul{
        display: none;
    }


    .nav__resposive-btn{
        display: flex;
    }

