  position: relative;

}

.contact__btn::before{

  content: '';
  background-color: #2d2d2d;
  background-color: var(--color-border);
  position: absolute;
  height: 17px;
  width: 80%;
  top:25px;
  left: 30px;
  z-index: -1;

}

.contact__btn::after{

  content: '';
  position: absolute;
  height: 17px;
  width: 0;
  top:25px;
  left: 30px;
  transition: all 0.2s ease;
  z-index: -1;


}

.contact__btn:hover::after{

